import cl from './Ques.module.scss'
import {FC, useState} from "react";

export const Ques = () => {
    return <section id={'ques'}>
        <div className="container">
            <div className={cl.main}>
                <div className="title">
                    <h1>  </h1>
                </div>
                <div className={cl.items}>
                    <div className={cl.item}>
                        <h2></h2>
                        <div className={cl.tabs}>
                            {[
                                ['  P2P ?', ',        ,     peer-to-peer.  peer-to-peer, ,    , P2P,      ,    .'],
                                ['     ?', 'P2P- -    P2P- (. peer-to-peer    )          .     ,        ( ).       ,   ,   ..,         .'],
                                [' ,    ?', ' Exnode P2P           .     - .          Exnode P2P      ,               .'],
                                ['   ?', '    15 .  ,    2  4 .  ,         ,      .'],
                                ['      ?', '       Exnode P2P          .'],
                            ].map((el, idx) => <Item title={el[0]} text={el[1]} key={idx}/>)}
                        </div>
                    </div>
                    <div className={cl.item}>
                        <h2></h2>
                        <div className={cl.tabs}>
                            {[
                                ['     ?',<>
                                    <p>  , ,   Exnode P2P,     .     :        .</p>
                                    <p>,   :</p>
                                    <ol style={{listStyle:'disc'}}>
                                        <li>    </li>
                                        <li>       </li>
                                        <li> ,         </li>
                                        <li>         </li>
                                        <li>         </li>
                                    </ol>
                                    <p>, ,  . :  .    ,    ,          Exnode P2P</p>
                                </>],
                                ['      ?', '         .    .'],
                                ['   ?', '     Exnode P2P,          ,          '],
                                ['     ?', '       '],
                                ['    ?', '        . ,     ,     - .'],
                            ].map((el, idx) => <Item title={el[0]} text={el[1]} key={idx}/>)}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
}


const Item: FC<{ title: any, text: any }> = ({title, text}) => {

    const [mode, setMode] = useState(false)

    return <div className={cl.tab}>
        <h2 onClick={() => setMode(prevState => !prevState)}>
            <span>{title}</span>
            <button className={mode ? cl.active : ''}>
                <img src="./assets/blueArrow.png" alt=""/>
            </button>
        </h2>
        <h3 className={mode ? cl.active : ''}>
            {text}
        </h3>
    </div>
}